.content{
  width: 100%;
  .title {
    font-size: 16px;
    font-weight: bold;
    color: #333333;
  }
  .searchBox {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 16px 0;
  }
  .listBox {
    width: 100%;
    height: calc(100vh - 128px);
    overflow-y: auto;
  }
  .createTaskBox {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: left;
    flex-wrap: wrap;
    align-items: flex-start;
    .createTask {
      width: 278px;
      height: 168px;
      border: 1px solid #E5E5E5;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 10px;
      text-align: center;
      cursor: pointer;
    }
    .itemTask {
      width: 278px;
      height: 168px;
      border: 1px solid #E5E5E5;
      border-radius: 10px;
      padding: 16px;
      box-sizing: border-box;
      cursor: pointer;
      .title {
        font-size: 14px;
        font-weight: bold;
        color: #333333;
      }
      .time {
        font-size: 12px;
        color: #999999;
        padding: 5px 0 10px 0;
      }
      .speedBox {
        display: flex;
        justify-content: space-between;
        font-size: 12px;
        color: #999999;
        span {
          font-weight: bolder;
          display: inline-block;
          padding-right: 10px;
        }
      }
      .status {
        font-size: 12px;
        color: #999999;
        padding: 5px 0 10px 0;
        span {
          font-weight: bolder;
          display: inline-block;
          padding-right: 10px;
        }
      }
      .btnBox {
        display: flex;
        justify-content: end;
      }
    }
  }
}
@primary-color: #1DA57A;