.container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  // background: url(../images/login_bg.png) no-repeat top center;
  // background-size: 100% 100%;
  border-radius: 10px;
  position: relative;
  .loginLef {
    position: absolute;
    left: 0;
    bottom: 0;
    img {
      width: 40%;
      height: auto;
    }
  }
  .loginRight {
    position: absolute;
    width: 70%;
    height: auto;
    right: 0;
    top: 0;
  }
  .loginBlock {
    position: absolute;
    width: 240px;
    top: 20%;
    left: 15%;

    .loginBlock1 {
      position: absolute;
      width: 240px;
      height: auto;
      z-index: 2;
      left: 0;
      top: 0;
    }

    .loginBlock2 {
      position: absolute;
      top: 30px;
      left: 40px;
      width: 160px;
      height: auto;
      z-index: 3;
      animation: jump 1s linear infinite;
      @keyframes jump {
        0% {
          transform: translateY(0);
        }
    
        50% {
          transform: translateY(-10px);
        }
    
        100% {
          transform: translateY(0);
        }
      }
    }
  }
  .loginC {
    position: absolute;
    width: 240px;
    left: 20%;
    top: 46%;
    .imgBlock3 {
      width: 240px;
      height: auto;
      z-index: 1;
      animation: rotate 1s linear infinite;
      @keyframes rotate {
        0% {
          transform: rotate(0deg);
        }
    
        100% {
          transform: rotate(360deg);
        }
      }
    }
  }
  .loginTitle {
    position: absolute;
    top: 50%;
    right: 15%;
    font-size: 40px;
    font-weight: bold;
    line-height: 20px;
  }
}
@primary-color: #1DA57A;